}
.ul-clear-style {
  margin: 0;
  padding: 0;
  list-style: none;
}
body {
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 30px;
  font-family: "Open Sans", "Arial", "Helvetica", "Garuda", sans-serif;
  color: #283645;
  font-style: normal;
  font-weight: 400;
  background-color: #ffffff;
  min-width: 320px;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.main-nav__toogle {
  position: absolute;
  top: 18px;
  right: 19px;
  width: 50px;
  height: 30px;
  background-color: transparent;
  border: none;
  outline: none;
}
@media (min-width: 640px) {
  .main-nav__toogle {
    top: 40px;
    margin: auto;
  }
}
@media (min-width: 960px) {
  .main-nav__toogle {
    display: none;
  }
}
.main-nav__toogle--burger {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 960px) {
  .main-nav__toogle--burger {
    display: none;
  }
}
.main-nav__icon {
  fill: white;
}
.main-nav__icon:hover {
  fill: #d22856;
}
.main-nav__icon:active {
  opacity: 0.3;
  fill: #d22856;
}
.site-list {
  display: flex;
  background-color: #283645;
  align-items: stretch;
  flex-direction: column;
  min-height: 62px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 960px) {
  .site-list {
    height: 110px;
    margin-right: 10px;
    margin-left: 170px;
    background-color: transparent;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
  }
}
.site-list__link {
  display: block;
  margin-top: auto;
  margin-bottom: auto;
  padding-top: 17px;
  padding-bottom: 17px;
  font-size: 18px;
  font-weight: 700px;
  line-height: 25px;
  color: white;
  flex-direction: column;
  text-decoration: none;
  text-transform: uppercase;
}
@media (min-width: 960px) {
  .site-list__link {
    margin: auto;
    margin-bottom: 25px;
    text-transform: none;
  }
}
.site-list__link:hover {
  color: #d22856;
}
.site-list__link:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 960px) {
  .site-list__link--active {
    position: relative;
  }
  .site-list__link--active::after {
    position: absolute;
    right: -5%;
    top: 88%;
    left: 0;
    height: 2px;
    background: white;
    content: "";
  }
}
.site-list__item {
  height: 62px;
  text-align: center;
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000000;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}
@media (min-width: 960px) {
  .site-list__item {
    border-top: none;
    border-bottom: none;
  }
}
.page-header {
